@media (max-width: 1023px) {
  .video::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 413px;
    background-color: #131314; }
  .video__title {
    margin: 0;
    margin-bottom: 20px;
    padding-top: 70px;
    font-size: 24px; }
  .video__description {
    width: 55.85%;
    margin: 0 auto;
    margin-bottom: 40px; }
  .video__container {
    position: relative;
    width: 65.49%;
    padding-bottom: 282px; }
  .video__img-container img {
    max-height: 282px; } }

@media (max-width: 767px) {
  .video::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 384px;
    background-color: #131314; }
  .video__title {
    padding-top: 84px; }
  .video__description {
    width: 90%;
    margin-bottom: 54px; }
  .video__container {
    position: relative;
    width: 90%;
    padding-bottom: 161px; }
  .video__img-container img {
    max-height: 161px; } }
